.lab {
    &__wrapper {
        padding: 30px 0;

        @include vp-767 {
            padding: 0 0 25px;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 40px;
        max-width: 576px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 20px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        padding: 0;
        @media screen and (max-width: 900px) {
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 500px) {
            grid-template-columns: 1fr;
        }
    }

    ul li::before {
        display: none;
    }

    &__item {
        background: rgba(246, 245, 245, 1);
        padding: 20px;
        border-radius: 3px;
        display: flex;
        flex-direction: column;
        gap: 20px;

        &::before {
            display: none;
        }

        @include vp-767 {
            padding: 16px;
        }
    }

    &__subtitle {
        font-family: 'Roboto';
        font-size: 18px;
        font-weight: 500;
        line-height: 140%;
        color: rgba(163, 16, 8, 1);
        margin: 0;

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 14px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: auto 0 0;

        @include vp-767 {
            font-size: 12px;
        }
    }
}