.how {
    background: rgba(246, 245, 245, 1);

    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 24px;
        }
    }

    &__text {
        
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 40px;

        a {
            color: rgba(163, 16, 8, 1);
            text-decoration: underline;
            transition: .3s;
            display: inline-block;
        }

        a:hover {
           opacity: 0.8;
        }

        @include vp-767 {
            margin-bottom: 30px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding: 0;
        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: rgba(255, 255, 255, 1);
        padding: 20px;
        border-radius: 3px;
        min-height: 120px;
        display: flex;
        flex-direction: column;

        @include vp-767 {
            padding: 16px;
        }

        &::before {
            display: none;
        }
    }

    &__item-text {
        font-weight: 600;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 15px;

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__link {
        
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        text-decoration: underline;
        color: rgba(163, 16, 8, 1);
        margin-top: auto;

        @include vp-767 {
            font-size: 14px;
        }
    }
}