.expert {
    &__wrapper {
        
        display: flex;
        flex-direction: column;
    }

    .expert__item:nth-child(even) {
        background: rgba(246, 245, 245, 1);
    }

    &__content {
        display: flex;
        justify-content: space-between;
        padding: 60px 0;
        gap: 20px;

        @include vp-767 {
            flex-direction: column;
            padding: 50px 0;
        }
    }

    .expert__item:nth-child(even) .expert__content {
        flex-direction: row-reverse;
        @include vp-767 {
            flex-direction: column;
            padding: 50px 0;
        }
    }

    &__info {
        max-width: 600px;
        width: 100%;

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 20px;

        @media screen and (max-width: 900px) {
            font-size: 24px;
        }
    }

    &__text {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 15px;
    }

    &__text:last-child {
        margin: 0;
    }


    &__image {
        max-width: 400px;
        width: 100%;
        height: auto;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-767 {
            max-width: 100%;
        }
    }


    span {
        display: block;
        font-size: 12px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 0.7);
        max-width: 400px;
        margin-top: 25px;

        @include vp-767 {
            max-width: 100%;
        }
    }
}