.turnon {
    background: rgb(246, 245, 245);
    margin: 40px 0;
    &__wrapper {
        padding: 60px 0;
    }

    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 50px;
    }

    &__title2 {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 20px;
    }

    &__subtext {
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 30px;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: rgb(255, 255, 255);
        padding: 20px;
        border-radius: 3px;
        display: flex;
        flex-direction: column;
    }

    &__name {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 20px;
    }

    &__text {
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0;
    }
}