.process {
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        
        margin: 0 0 50px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 50px;
        counter-reset: item-counter;
        padding-left: 80px;

        @include vp-767 {
            padding-left: 60px;
        }
    }

    &__item {
        counter-increment: item-counter; 
        position: relative; 
        padding-left: 40px; 

        @include vp-767 {
            padding-left: 30px;
        }
    }

    &__item::before {
        content: counter(item-counter); 
        position: absolute;
        left: -60px;
        top: -20px;
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 128px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: #a31008;

        @include vp-767 {
            font-size: 110px;
        }

        @include vp-389 {
            top: -10px;
        }
    }

    &__name {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 26px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 24px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0;

        @include vp-767 {
            font-size: 20px;
        }
    }
}