.text {
    @include vp-767 {

        &.text--mobile-bg {
            background: rgba(246, 245, 245, 1);
        }
        
    }

    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 60px 0 20px;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 32px;
        line-height: 130%;
        font-weight: 700;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 20px;
        max-width: 900px;

        @include vp-767 {
            font-size: 22px;
        }

        @include vp-389 {
            font-size: 18px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        max-width: 589px;
        margin: 0;
    }
}