div#primary .top * {
    line-height: normal !important; /*    */
}

.top {
    line-height: 1.5 !important;
    position: relative; 
    overflow: hidden;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.5) 0%,
        rgba(0, 0, 0, 0.7) 70%,
        rgba(0, 0, 0, 0.9) 100%
      );
      z-index: 1;  
    }
    
  
    &__image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      img:last-child {
        display: none;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      @include vp-767 {
        top: -300px;

        img:first-child {
            display: none;
        }

        img:last-child {
            display: block;
        }
      }
    }
  
    &__content {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: flex-start;
      position: relative;
      z-index: 1;
      gap: 15px;

      @include vp-767 {
        flex-direction: column;
        gap: 0;
      }
    }
  
    &__left {
        width: 62%;
        padding-top: 60px;
        padding-bottom: 60px;
        @media screen and (max-width: 1130px) {
            width: 100%;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 50px;
        line-height: 120%;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 33px;

        @media screen and (max-width: 900px) {
            font-size: 40px;
        }

        @include vp-767 {
            font-size: 28px;
            margin-bottom: 20px;
        }
    }

    &__list {
        list-style: disc;
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 45px;

        @include vp-767 {
            margin-bottom: 30px;
        }
    }

    &__item {
        list-style: disc;
        color: rgba(255, 255, 255, 1);

        &::before {
            display: none;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 120%;
        color: rgba(255, 255, 255, 1);
        margin: 0;

        @media screen and (max-width: 768px) {
            font-size: 14px;
            line-height: 110%;
        }
    }

    &__box {
        display: flex;
        align-items: center;
        gap: 30px;
        @include vp-767 {
            margin-bottom: 60px;
        }


        @include vp-389 {
            flex-direction: column;
            align-items: flex-start;
        }
    }
  
    &__right {
        margin: 40px 0 30px;
        padding: 20px;
        width: 50%;
        background: rgba(255, 255, 255, 1);
        border-radius: 4px;
        max-width: 350px;

        @include vp-767 {
            margin: 0 !important;
            max-width: 100% !important;
            width: 100% !important;
            position: relative !important;
            padding: 50px 0px 25px !important;
            &::after {
                content: '';
                position: absolute;
                left: -20px;
                top: 0;
                right: 0;
                bottom: 0;
                width: 100vw;
                height: 100%;
                background: rgba(255, 255, 255, 1);
                z-index: -1;
                
            }
        }
    }
  
    &__form-title {
        font-family: 'Roboto';
        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
        color: rgba(33, 33, 33, 1);
        max-width: 300px;
        margin: 0 0 20px;

        @include vp-767 {
            max-width: 100% !important;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
    }

    .custom-input input,
    .custom-textarea textarea {
        border: 1px solid rgba(211, 209, 209, 1);
        border-radius: 3px;
        height: 45px;
        padding: 13px 20px 13px 10px;
        color: rgba(33, 33, 33, 1);
        resize: none; 
        overflow-y: hidden; 
        font-family: 'Roboto';
        font-size: 14px;
        line-height: 130%;
        font-weight: 500;
        min-height: auto;
    }

    .custom-input input::placeholder,
    .custom-textarea textarea::placeholder {
        font-family: 'Roboto';
        font-size: 14px;
        line-height: 130%;
        color: rgba(211, 209, 209, 1);
        font-weight: 400;
    }

  
    &__btn {
        max-width: 100%;
        width: 100%;
        padding: 12px;
        background: rgba(163, 16, 8, 1);
        color: rgba(255, 255, 255, 1);
        border: none;
        border-radius: 3px;
        cursor: pointer;
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 130%;
        margin-bottom: 10px;
        transition: 0.3s;
    
        &:hover {
            opacity: 0.8;;
        }
    }
  
    &__agreement {
        font-family: 'Roboto';
        font-size: 12px;
        line-height: 130%;
        color: rgba(33, 33, 33, 1);
        margin: 0;
      a {
        display: inline;
        text-decoration: underline;
        color: rgba(33, 33, 33, 1);
      }
    }
}