.where {
    &__wrapper {
        padding: 40px 0;
    }

    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 40px;
    }

    &__list {
        display: flex;
        justify-content: center;
        gap: 20px;
        flex-wrap: wrap;
    }

    &__item {
        flex-basis: 511px;
        background: #f6f5f5;
        padding: 15px 20px;
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 24px;
        leading-trim: NONE;
        line-height: 110%;
        letter-spacing: 0%;

        span {
            font-weight: 700;
        }
    }
}