.intro-block {
    &__wrapper {
        padding: 60px 0 30px;
        display: flex;
        justify-content: space-between;
        gap: 20px;

        @include vp-767 {
            flex-direction: column;
        }
    }

    &__text-content {
        width: 50%;

        @include vp-767 {
            width: 100%;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0;
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 511/312;
        border-radius: 5px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}