.audit-card {
    background: rgba(246, 245, 245, 1);
    
    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding: 0;
        @include vp-767 {
            grid-template-columns: 1fr;   
        }
    }

    &__item {
        padding: 20px;
        background: rgba(255, 255, 255, 1);
        border-radius: 3px;
        display: flex;
        flex-direction: column;
        height: 100%;
        flex-grow: 1;

        &::before {
            display: none;
        }

        &--big {
            padding: 0;
            background: transparent;
            flex-direction: row;
            justify-content: space-between;
            grid-column: 1/-1;
            gap: 20px;
            
            @include vp-767 {
                flex-direction: column;
            }
            
            .audit-card__info {
                background: rgba(255, 255, 255, 1);
                padding: 20px;
                border-radius: 3px;
                display: flex;
                flex-direction: column;
                width: 75%;

                @include vp-1023 {
                    width: 50%;
                }

                @include vp-767 {
                    width: 100%;
                }
            }

            .audit-card__image {
                max-width: 100%;
                height: auto;
                aspect-ratio: 320/340;
                border-radius: 3px;
                overflow: hidden;
                width: 25%;

                @include vp-1023 {
                    width: 50%;
                }

                
                @include vp-767 {
                    width: 100%;
                    aspect-ratio: 300/220;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

            }
        }


    }

    &__subtitle {
        font-family: Roboto;
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(163, 16, 8, 1);
        margin: 0 0 20px;

    }

    &__text {
        font-family: Roboto;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: auto 0 0;
       
    }
}