.marking {
    background: rgba(246, 245, 245, 1);
    &__wrapper {
        padding: 60px 0;
        display: flex;
        justify-content: space-between;
        gap: 20px;

        @include vp-767 {
            padding: 50px 0;
            flex-direction: column;
            gap: 30px;
        }
    }

    &__info {
        max-width: 650px;
        width: 70%;

        @include vp-767 {
            width: 100%;
            max-width: 100%;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 24px;
        }
    }

    &__text {
       
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 16px;
    }

    span {
        
        font-size: 12px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 0.7);
        display: block;
    }

    &__image {
        max-width: 420px;
        height: auto;
        width: 50%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-767 {
            width: 100%;
            max-width: 100%;
        }
    }
}