.part {
    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 24px;
        }
    }

    &__text {
        
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 40px;
        display: none;
        a {
            color: rgba(163, 16, 8, 1);
            text-decoration: underline;
            transition: .3s;
            display: inline-block;
        }

        a:hover {
           opacity: 0.8;
        }

        @include vp-767 {
            display: block;
            margin-bottom: 30px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        margin-bottom: 20px;
        padding: 0;
        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: rgba(246, 245, 245, 1);
        padding: 20px;
        border-radius: 3px;
        min-height: 120px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        min-height: 180px;
        @include vp-767 {
            min-height: auto;
            padding: 16px;
        }

        &::before {
            display: none;
        } 
    }

    &__item-text {
        font-weight: 600;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 15px;

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__btn {
        padding: 0;
        border: none;
        font-weight: 600;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        text-decoration: underline;
        color: rgba(163, 16, 8, 1);
        margin-top: auto;
        transition: .3s;

        &:hover {
            opacity: 0.8;
        }

        @include vp-767 {
            font-size: 14px;
        }
    }

    span {
        
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        display: block;
        max-width: 475px;

        @include vp-767 {
            display: none;
        }
    }
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.7);
    z-index: 1000;
    display: none;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 40px; 
    border-radius: 8px;
    width: 650px; 
    max-height: 80vh;
    overflow-y: auto;
    z-index: 1001;
    display: none;
    box-shadow: 0 4px 20px rgba(0,0,0,0.15);

    @include vp-767 {
        max-width: 450px;
        padding: 40px 16px;
    }

    @media screen and (max-width: 470px) {
        max-width: 350px;
    }

    @media screen and (max-width: 360px) {
        max-width: 315px;
    }

    &__close {
        position: absolute;
        top: 0px;
        right: 20px;
        font-size: 45px;
        background: none;
        border: none;
        cursor: pointer;
        color: #666;
        transition: color 0.2s;
        padding: 0;
        &:hover {
          color: #000;
        }
    }

    &__content {
        pointer-events: auto;
    }


    &__title {
        font-weight: 600;
        font-size: 32px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(163, 16, 8, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 24px;
        }
    }

    &__subtitle {
        
        font-weight: 600;
        font-size: 20px;
        line-height: 130%;
        letter-spacing: 0%;
        color: rgba(122, 15, 1, 1);
        margin: 0 0 20px;

        @include vp-767 {
            font-size: 16px;
        }
    }
    
    &__list {
      
      padding-left: 20px;
      list-style: disc;
      li {
        list-style: disc;
        margin-bottom: 10px;

        &::before {
            display: none;
        }
      }
    }
    
    &__person {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        margin: 0;

        @include vp-767 {
            font-size: 14px;
        }
    }
}

body.modal-open {
    overflow: hidden;
}