.fruit {
    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 25px 0;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 32px;
        font-weight: 700;
        line-height: 130%;
        color: rgba(33, 33, 33, 1);
        margin: 0 0 40px;
        max-width: 700px;

        @include vp-767 {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }

    &__subtitle {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 20px;
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding: 0;
        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        background: rgba(246, 245, 245, 1);
        padding: 20px;
        display: flex;
        align-items: center;
        gap: 16px;
        &::before {
            display: none;
        }

        @include vp-767 {
            flex-direction: column;
            align-items: start;
            padding: 16px;
        }
    }

    &__image {
        width: 50px;
        height: 50px;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__item-text {
        font-family: 'Roboto';
        font-size: 14px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0;
    }
}