.mode {
    background: rgba(246, 245, 245, 1);

    &__wrapper {
        padding: 60px 0;
        display: flex;
        justify-content: space-between;
        gap: 30px;

        @include vp-767 {
            flex-direction: column;
            justify-content: flex-start;
        }
    }

    &__info {
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 25px;

        @include vp-767 {
            width: 100%;
            gap: 16px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: 20px;

        @include vp-767 {
            gap: 16px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0;
    }

    span {
        display: block;
        max-width: 550px;
        font-family: 'Roboto';
        font-size: 20px;
        font-weight: 500;
        line-height: 140%;
        color: rgba(163, 16, 8, 1);

        @include vp-767 {
            max-width: 100%;
            font-size: 16px;
        }
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 400/340;

        @include vp-767 {
            aspect-ratio: 300/220;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }


    }
}