.varia {
    background: rgba(246, 245, 245, 1);
    &__wrapper {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 30px;

        @include vp-767 {
            padding: 50px 0;
            flex-direction: column;
            gap: 30px;
        }
    }

    &__info {
        width: 50%;
        padding: 60px 0;

        @include vp-767 {
            width: 100%;
            padding: 0;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 20px;
        line-height: 140%;
        font-family: 500;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 20px;
        max-width: 500px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 16px;
            max-width: 100%;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0;

        @include vp-767 {
            font-size: 14px;
        }
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 400/460;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-767 {
            aspect-ratio: 300/200;
        }
    }
}