.comon {
    background: rgb(246, 245, 245);
    margin: 40px 0;
    &__wrapper {
        padding: 70px 0;
    }

    
    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 50px;
    }

    &__text {
        font-family: 'Roboto';
        font-weight: 500;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;

    }
    
}