.sides {
    &__wrapper {
        padding: 90px 0 40px;
        display: flex;
        gap: 40px;

        @media screen and (max-width: 900px) {
            flex-direction: column;
            gap: 30px;
        }
    }

    &__info {
        width: 50%;

        @media screen and (max-width: 900px) {
            width: 100%;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 20px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
        list-style: disc;
        padding-left: 20px;
    }

    &__item {
        list-style: disc;
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;

        span {
            font-weight: 700;
        }
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 611/348;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}