.plant {
    background: rgba(246, 245, 245, 1);

    &__wrapper {
        padding: 60px 0;

        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        font-family: Roboto;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        color: rgba(163, 16, 8, 1);
        margin: 0 0 40px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 20px;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__top {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        display: grid;
        grid-template-columns: 0.5fr 1fr;
        align-items: center;
        gap: 16px;
        border-radius: 3px;
        padding: 20px;
        background: rgba(255, 255, 255, 1);

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__image {
        width: 120px;
        height: 100px;
        // max-width: 100%;
        // height: auto;
        // aspect-ratio: 120/100;
        @include vp-767 {
            width: 100%;
            height: 280px;   
            
        }
        // @include vp-389 {
        //     // aspect-ratio: 200/100;
        //     width: 200px;
        //     height: 100px;
        // }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__text {
        font-family: Roboto;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0;
        max-width: 334px;

        @include vp-767 {
            max-width: 100%;
        }
    }

    &__bottom {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;

        @media screen and (max-width: 1024px) {
            grid-template-columns: 1fr 1fr;
        }
        
        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }
}