.pack {
    &__wrapper {
        padding: 0 0 60px;
        
        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 25px;

        @include vp-767 {
            font-size: 16px;
            margin-bottom: 20px;
        }
    }

    &__info {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;

        @include vp-767 {
            grid-template-columns: 1fr;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr;
        gap: 20px;
        padding: 0;
    }

    &__item {
        background: rgba(246, 245, 245, 1);
        border-radius: 3px;
        padding: 20px;

        &::before {
            display: none;
        }
    }

    &__item-text {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(33, 33, 33, 1);
        margin: 0;
    }

    &__block {
        background: rgba(246, 245, 245, 1);
        border-radius: 3px;
        padding: 20px;
        display: flex;
        flex-direction: column;
        
        @include vp-767 {
            .pack__item-text {
                font-weight: 600;
                font-size: 18px;
                margin-bottom: 20px;
            }
        }
    }

    &__item-list {
        list-style: disc;
        padding-left: 15px;
        margin-top: auto;
    }

    &__item-item {
        list-style: disc;

        &::before {
            display: none;
        }
    }

    &__item-item::marker {
        font-size: 13px;
    }

    &__item-item:last-child .pack__item-item-text {
        margin: 0;
    }

    &__item-item-text {
        font-size: 14px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 5px;
    }

    
}