.doc {
    background: rgba(246, 245, 245, 1);

    &__wrapper {
        margin: 30px 0 0;
        padding: 60px 0;

        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 25px;
        font-weight: 400;
        max-width: 690px;

        @include vp-767 {
            font-size: 15px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 20px;
        font-weight: 700;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 40px;
        max-width: 620px;

        @include vp-767 {
            font-size: 16px;

        }
    }

    &__box-list {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__box {
        background: rgba(255, 255, 255, 1);
        padding: 20px;
        border-radius: 3px;

        @include vp-767 {
            padding: 16px;
        }
    }

    &__box-title {
        font-family: 'Roboto';
        font-size: 24px;
        font-weight: 700;
        line-height: 130%;
        color: rgba(163, 16, 8, 1);
        margin: 0 0 40px;

        @include vp-389 {
            font-size: 18px;
            margin-bottom: 16px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 10px 20px;
        list-style: disc;
        padding-left: 20px;

        @include vp-1023 {
            grid-template-columns: 1fr 1fr 1fr;
        }

        @include vp-767 {
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 520px) {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        list-style: disc;
        
        &::before {
            display: none;
        }

        &::marker {
            font-size: 10px;
        } 
    }

    &__item-text {
        font-family: 'Roboto';
        font-size: 14px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0;
        max-width: 235px;
    }
}