.pic-work {
    background: rgba(246, 245, 245, 1);
    &__wrapper {
        padding: 60px 0;
        @include vp-767 {
            padding: 50px 0;
        }
    }

    &__title {
        
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: 0 0 40px;
        max-width: 670px;

        @include vp-767 {
            
            font-size: 16px;
            margin-bottom: 20px;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        padding: 0; 
        @include vp-767 {
            grid-template-columns: 1fr;
            gap: 16px;
        }
    }

    &__item {
        background: rgba(255, 255, 255, 1);
        padding: 20px;
        border-radius: 3px;
        display: flex;
        flex-direction: column;

        @include vp-767 {
            padding: 16px;
        }

        &::before {
            display: none;
        } 
    }

    &__item-title {
        
        font-weight: 600;
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(163, 16, 8, 1);
        margin: 0 0 80px;

        @include vp-767 {
            margin: 0 0 16px;
        }
    }

    &__item-text {
        
        font-size: 14px;
        
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(0, 0, 0, 1);
        margin: auto 0 0;
        @include vp-767 {
            margin: 0;
        }
    }
}