.pic-mode {
    &__wrapper {
        padding: 60px 0;
        display: flex;
        justify-content: space-between;
        gap: 30px;

        @include vp-767 {
            flex-direction: column;
            justify-content: flex-start;
        }
    }

    &__title {
        
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0%;
        margin: 0;
        color: rgba(0, 0, 0, 1);

        a {
            display: inline-block;
            color: rgba(163, 16, 8, 1);
            text-decoration: underline;
            transition: .3s;
        }

        a:hover {
            opacity: 0.8;
        }

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__info {
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 25px;

        @include vp-767 {
            width: 100%;
            gap: 16px;
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: 20px;

        @include vp-767 {
            gap: 16px;
        }
    }

    &__text {
        font-family: 'Roboto';
        font-size: 16px;
        line-height: 140%;
        color: rgba(0, 0, 0, 1);
        margin: 0;

        @include vp-767 {
            font-size: 14px;
        }
    }


    &__image {
        width: 40%;
        max-width: 100%;
        height: auto;
        aspect-ratio: 400/340;
        border-radius: 5px;
        overflow: hidden;
        @include vp-1023 {
            width: 50%;
        }

        @include vp-767 {
            width: 100%;
            aspect-ratio: 300/220;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }


    }
}