.gaz {
    
    &__wrapper {
        padding: 60px 0;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 80px;

        @include vp-767 {
            gap: 50px;
        }
    }

    &__item {
        display: flex;
        gap: 40px;

        @include vp-767 {
            flex-direction: column;
            gap: 20px;
        }
    }

    &__item:nth-child(2n) {
        flex-direction: row-reverse;

        @include vp-767 {
            flex-direction: column;
        }
    }

    &__info {
        width: 50%;

        @include vp-767 {
            width: 100%;
        }
    }

    &__name {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0 0 15px;
        color: #a31008;
        @include vp-767 {
            font-size: 24px;
        }
    }

    &__desc {
        font-family: 'Roboto';
        font-weight: 400;
        font-style: Regular;
        font-size: 20px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        margin: 0;

        @include vp-767 {
            font-size: 18px;
        }
    }

    &__image {
        max-width: 100%;
        height: auto;
        aspect-ratio: 611/336;
        border-radius: 5px;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}