.other {
    &__wrapper {
        padding: 30px 0 70px;
    }

    &__title {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Bold;
        font-size: 32px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 50px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 30px 20px;

        @media screen and (max-width: 950px) {
            grid-template-columns: repeat(4, 1fr);
        }

        @include vp-767 {
            grid-template-columns: repeat(3, 1fr);
        }

        @media screen and (max-width: 600px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media screen and (max-width: 450px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__item {
        background-color: #f6f5f5;
        border-radius: 5px;
        padding: 15px 18px;
        min-height: 88px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__link {
        font-family: 'Roboto';
        font-weight: 700;
        font-style: Regular;
        font-size: 14px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        color: #a31008;
        transition: .3s;
    }

    &__link:hover {
        opacity: 0.8;
    }

}